import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class Landing extends Component {
  render() {
    return (
      <div style={{ width: '100%', margin: 'auto' }}>
        <Grid className="landing-grid">
          <Cell col={12}>
            <img
              src="../images/avatar.jpg"
              alt="avatar"
              className="avatar-img"
            />
            <div className="banner-text">
              <h1>Junior Developer</h1>
              <hr />
              <p>JAVA | SQL | C++ | HTML/CSS | JAVASCRIPT | REACT | NODEJS </p>

              <div className="social-links">
                { /* LinkedIn */}
                <a href="https://www.linkedin.com/in/michail-leonidou-02a515152/ " rel="noopener noreferrer " target="_blank">
                  <i className="fa fa-linkedin-square" aria-hidden="true" />
                </a>

                { /* Github */}
                <a href="https://github.com/MichaiLeonidou95 " rel="noopener noreferrer " target="_blank">
                  <i className="fa fa-github-square" aria-hidden="true" />
                </a>

                { /* Freecodecamp */}
                <a href="https://www.freecodecamp.org/michaileonidou " rel="noopener noreferrer " target="_blank">
                  <i className="fa fa-free-code-camp" aria-hidden="true" />
                </a>

              </div>
            </div>
          </Cell>

        </Grid>
      </div>
    )
  }

}

export default Landing;